<template>
	<view style="overflow: hidden;">
		<view class="yiny" v-if="showAction" @click="animationHide"></view>
		<view class="showActionSheet" :animation="animationData" v-if="showAction">
			<view class="item" @click="photo">相册</view>
			<view class="item" @click="shoot">拍照</view>
			<!-- <view class="item" @click="animationHide">取消</view> -->
		</view>
	</view>
	
</template>

<script>
	export default{
		data(){
			return{
				animationData:{},
				showAction:false,
			}
		},
		props: {
			imgList: {
				type: Array,
				default: () => {
					return []
				}
			},
			langth: {
				type: Number,
				default: () => {
					return 
				}
			}
		},
		methods:{
			photo(){
				uni.chooseImage({
					count: this.langth-this.imgList.length,
					sizeType: ['original', 'compressed'],
					sourceType: ['album'],
					success:  res=> {
						
						for(var i=0;i<res.tempFilePaths.length;i++){
							this.imgList.push(res.tempFilePaths[i])
						}
						this.showAction=false
					}
				});
			},
			shoot(){
				uni.chooseImage({
					count: this.langth-this.imgList.length, //默认9
					sizeType: ['original','compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['camera'], //从相册选择
					success:  res=> {
						
						for(var i=0;i<res.tempFilePaths.length;i++){
							this.imgList.push(res.tempFilePaths[i])
						}
						this.showAction=false
					}
				});
			},
			animationShow(){
				this.showAction=true
				var animation = uni.createAnimation({
					  duration: 500,
						timingFunction: 'ease',
					})
				this.animation = animation
				animation.scale(0,0).step()
				this.animationData = animation.export()
			
				setTimeout(function() {
				  animation.scale(1,1).step()
				  this.animationData = animation.export()
				}.bind(this), 100)
			},
			animationHide(){
				var that=this;
				var animation = uni.createAnimation({
					  duration: 300,
						timingFunction: 'ease',
					})
				this.animation = animation
				animation.scale(1,1).step()
				this.animationData = animation.export()
				animation.scale(0,0).step()
				this.animationData = animation.export()
				setTimeout(function() {
				  that.showAction=false;
				}.bind(this), 300)
			},
		}
	}
</script>

<style lang="scss">
	.yiny{
		position: fixed;
		z-index: 999;
		top: 0;
		// right: 0;
		left: 0;
		// bottom: 0;
		background: rgba(0,0,0,.6);
		height: 100vh;
		width: 100%;
	}
	.showActionSheet{
		width: 80%;
		z-index: 999;
		overflow: hidden;
		border-radius:10upx ;
		position: fixed;
		bottom: 40%;
		left:10%;
		.item{
			width: 100%;
			height: 100upx;
			line-height: 100upx;
			border-bottom:1px solid #eee;
			text-align: center;
			background: #fff;
		}
	}
</style>
